import React, { useState } from 'react';
import { Button, CircularProgress, Snackbar, Alert } from '@mui/material';

const Dashboard = ({ user }) => {
  const [isLoading, setIsLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [successMessage, setSuccessMessage] = useState('');

  const handleAutoApply = async () => {
    setIsLoading(true);
    setErrorMessage('');
    setSuccessMessage('');

    try {
      const response = await fetch('http://localhost:3001/api/start-auto-apply', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ userId: user.id, criteria: user.jobPreferences }),
      });

      const data = await response.json();

      if (response.ok) {
        setSuccessMessage('Auto-apply process started! Check your dashboard for updates.');
      } else {
        setErrorMessage(data.message || 'Error starting auto-apply.');
      }
    } catch (error) {
      setErrorMessage('Error communicating with server.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div>
      <h2>Dashboard</h2>
      <Button
        variant="contained"
        color="primary"
        onClick={handleAutoApply}
        disabled={isLoading}
      >
        {isLoading ? <CircularProgress size={24} color="inherit" /> : 'Start Auto-Apply'}
      </Button>

      {/* Success Snackbar */}
      <Snackbar open={!!successMessage} autoHideDuration={6000} onClose={() => setSuccessMessage('')}>
        <Alert onClose={() => setSuccessMessage('')} severity="success">
          {successMessage}
        </Alert>
      </Snackbar>

      {/* Error Snackbar */}
      <Snackbar open={!!errorMessage} autoHideDuration={6000} onClose={() => setErrorMessage('')}>
        <Alert onClose={() => setErrorMessage('')} severity="error">
          {errorMessage}
        </Alert>
      </Snackbar>
    </div>
  );
};

export default Dashboard;
